@media (min-width:1000px){

    .container{
        display: grid;
        grid-template-columns: 1fr 13rem;
        grid-template-rows: auto 1fr 7rem;
    }

    .content1{
        grid-column: 1/3;
        grid-row: 1/3;
    }

    .content{
        grid-column: 1/2;
        grid-row: 1/3;
    }  

    footer{
        grid-column: 1/3;
        grid-row: 3/4;
    }

}